import { toast } from "react-toastify";
import instance from "./instance";
import * as RequestApi from "./Requests";

// login api
export const loginApi = async (data) => {
  try {
    const response = await instance.post(RequestApi.authRequest.login, data);
    return response;
  } catch (err) {
    if (err && err.response && err.response.data && err.response.data.error) {
      toast.error(err.response.data.error);
    }
  }
};

// add driver
export const AddDriverApi = async (token, data) => {
  try {
    const response = await instance.post(
      RequestApi.driverRequest.addDriver,
      data,
      {
        headers: {
          token: token,
        },
      }
    );
    return response;
  } catch (err) {
    if (err && err.response && err.response.data && err.response.data.error) {
      toast.error(err.response.data.error);
    }
  }
};

// get driver
export const GetDriverApi = async (token, page, limit) => {
  try {
    const response = await instance.get(
      `${RequestApi.driverRequest.driverList}?page=${page || 1}&limit=${limit || 10}`,
      {
        headers: {
          token: token,
        },
      }
    );
    return response;
  } catch (err) {
    if (err && err.response && err.response.data && err.response.data.error) {
      toast.error(err.response.data.error);
    }
  }
};

