// Delete driver
export const DeleteDriverApi = async (token, data, is_friday_drivers) => {
  console.log(data.id);
  try {
    const response = await instance.post(
      `${RequestApi.driverRequest.deleteDriver}`,
      {
        id: data.id,
        is_friday_drivers: is_friday_drivers,
      },
      {
        headers: {
          token: token,
        },
      }
    );
    return response;
  } catch (err) {
    if (err && err.response && err.response.data && err.response.data.error) {
      toast.error(err.response.data.error);
    }
  }
};

