import React, { useState } from "react";
// history import
import { useNavigate } from "react-router-dom";
// import bootstrap
import { Form, Button } from "react-bootstrap";
// import taster
import { toast } from "react-toastify";

// import api calls
import { loginApi } from "../../ApiRequests/index";
// import svg file as reaqct component
import { EyeIcon, EyeOffIcon } from "../../Components/utils/Icons/Login";
// import scss file
import "./style.scss";

const LoginPage = () => {
  // use state manage
  const [isPasswordShow, setIsPasswordShow] = useState(false); // using this state visible/unvisible password
  const [loginInfo, setLoginInfo] = useState({ email: "", password: "" }); //this state is use for manage a email and password field
  
  const navigate = useNavigate();

  // useEffect(() => {
  //   let authToken = localStorage.getItem("auth");
  //   if(authToken){
  //     navigate("/dashboard/driver");
  //   }
  // }, [])

  // handle email and pass change events
  const handleOnChange = (e) => {
    const { name, value } = e.target;
    const loginData = Object.assign({}, loginInfo);
    loginData[name] = value;
    setLoginInfo(loginData);
  };

  // this functiuon is use when user is click on login
  const handleSubmitFunction = (e) => {
    // to prevent a form load after submit
    e.preventDefault();
    // check email and password in local state
    if (loginInfo.email && loginInfo.password) {
      // call an api for login
      loginApi(loginInfo)
        .then((res) => {
          // check if api response is success or not
          if (res?.data?.success) {
            // below commented line is use for get a user data
            // const stringyfyData = JSON.stringify(res.data.data);
            // check condition for token is available or not
            if (res.data.token) {
              const stringyfyDataToken = JSON.stringify(res.data.token);
              // store stringify token in local storage 
              localStorage.setItem("auth", stringyfyDataToken);
              // redirect to dashboard
              navigate("/dashboard/driver");
            } else {
              toast.error("Somethig went wrong! Please try after sometime");
            }
          }
        })
        .catch((err) => {
          console.log(err);
        });
    }
  };




  return (
    <React.Fragment>
      <div className="LoginPage">
        <div className="LoginPage__container">
          <div className="LoginPage__container--header">
            {/* <p className="m-0">Truck Tracking</p> */}
            <img src="/assets/image/logo-large-callahead.png" alt="collahead_logo"></img>
          </div>
          <div className="LoginPage__container--content">
            <Form onSubmit={handleSubmitFunction}>
              <Form.Label className="LoginPage__container--lable">
                Sign in
              </Form.Label>
              {/* email field */}
              <Form.Group className="mb-4" controlId="formBasicEmail">
                <Form.Control
                  className="Form_control"
                  type="email"
                  name="email"
                  onChange={handleOnChange}
                  placeholder="Enter email"
                />
              </Form.Group>
              {/* password field */}
              <Form.Group className="mb-4" controlId="formBasicPassword">
                <div className="Password__group">
                  <Form.Control
                    className="Form_control"
                    name="password"
                    onChange={handleOnChange}
                    type={isPasswordShow ? "text" : "password"}
                    placeholder="Enter password"
                  />
                  <div className="Password__group--icon">
                    {/* this condition is use for visible/unvisible password */}
                    {isPasswordShow ? (
                      <EyeOffIcon
                        onClick={() => {
                          setIsPasswordShow(false);
                        }}
                      />
                    ) : (
                      <EyeIcon
                        onClick={() => {
                          setIsPasswordShow(true);
                        }}
                      />
                    )}
                  </div>
                </div>
                {/* <Form.Text className="text-muted ">
                  Never share your email and password with anyone else.
                </Form.Text> */}
              </Form.Group>
              <Button
                className="mt-2 w-100 Form_btn"
                variant="primary"
                type="submit"
              >
                Sign in
              </Button>
            </Form>
          </div>
        </div>
      </div>
    </React.Fragment>
  );
};

export default LoginPage;
